<template>
  <div class="service-list">
    <div v-title>{{ title }}</div>
    <mt-navbar v-model="selected" :value="selected">
      <mt-tab-item id="pageAll">全部</mt-tab-item>
      <mt-tab-item id="pageWaitingUse">待启用</mt-tab-item>
      <mt-tab-item id="pageUsed">使用中</mt-tab-item>
      <mt-tab-item id="pageInvalid">已失效</mt-tab-item>
    </mt-navbar>
    <!--tab-container-->
    <mt-tab-container v-model="selected" ref="wrapper">
      <mt-tab-container-item :id="selected">
        <mt-loadmore
          v-if="serviceList.length"
          :autoFill="false"
          :top-method="loadTop"
          :bottom-method="loadBottom"
          @bottom-status-change="handleBottomChange"
          :bottom-all-loaded="allLoaded"
          ref="loadmore">
          <div class="service-info" v-for="(item, index) in serviceList" :key="index">
            <div class="com-list">
              <p>
                <span class="text-black">服务站名称</span>
                <span class="text-right text-red" v-if="true">距离失效还有3天24小时40分</span>
                <span class="text-right text-green" v-else-if="false">距离启用还有3天24小时40分</span>
                <span class="text-right text-gray" v-else-if="false">已失效</span>
              </p>
              <p>
                <span class="">回收方式&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span class="text-gray" v-if="true">线上</span>
                <span class="text-gray" v-else-if="false">线下</span>
              </p>
              <p>
                <span class="text-orange">当日订单&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span class="text-orange">36</span>
              </p>
            </div>
            <div class="order">
              <p>
                <span class="text-gray">累积订单：2800</span>
                <span class="text-orange text-right">查看订单</span>
              </p>
            </div>
          </div>
        </mt-loadmore>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
  import { Navbar, TabItem, TabContainer, TabContainerItem, Loadmore, Toast } from 'mint-ui'
  export default {
    data () {
      return {
        title: '服务站列表',
        selected: this.$route.query.selectedId,
        serviceList: [1, 0],
        allLoaded: false,
        serviceListTotal: 0, //  第一次请求数据是，返回的列表总数据
        pageNum: 1, //  页数，默认1
        pageSize: 10 //  一页显示记录数，默认10
      }
    },
    components: {
      [Navbar.name]: Navbar,
      [TabItem.name]: TabItem,
      [TabContainer.name]: TabContainer,
      [TabContainerItem.name]: TabContainerItem,
      [Loadmore.name]: Loadmore
    },
    created () {
      this.token = localStorage.getItem('token')
    },
    watch: {
      selected: function (val, oldVal) {
        // 请求接口刷新数据，要重新刷新列表数据，同时重置分页参数
        this.serviceList = ''
        this.pageNum = 1
        this.getServiceList()
      }
    },
    methods: {
      getServiceList () {
        // 切换tab，恢复，数据已全部获取完毕标识
        this.allLoaded = false
//        ServiceList({
//          pageNum: this.pageNum,
//          pageSize: this.pageSize,
//          m: this.selected
//        }).then(res => {
//          if (res.status === 'true') {
//            this.serviceList = res.info.result
//            this.serviceListTotal = res.info.total
//            Toast('已刷新')
//            this.$refs.loadmore.onTopLoaded()
//            下拉仅展示第一页的数据
//            this.pageNum = 1
//          } else {
//            // 请求失败的时候，请求数据的接口，显示网络错误
//            Toast(res.msg)
//          }
//        })
      },
      loadTop () {
        setTimeout(() => {
          //        ServiceList({
//          pageNum: this.pageNum,
//          pageSize: this.pageSize,
//          m: this.selected
//        }).then(res => {
//          if (res.status === 'true') {
//            this.serviceList = res.info.result
//            this.serviceListTotal = res.info.total
//            // 如果请求回来的数据，只有一页，就禁止上拉加载
//            if (this.serviceListTotal <= 10) {
//              this.allLoaded = true
//             } else {
//              this.pageNum++
//            }
//          } else {
//            // 请求失败的时候，请求数据的接口，显示网络错误
//          }
//        })
        }, 1500)
      },
      loadBottom () {},
      handleBottomChange () {}
    }
  }
</script>

<!-- 设置mint-ui组件的样式，style 标签去掉scoped -->
<style lang="less">
  .service-list .mint-tab-item-label {
    font-size: 16px;
  }
</style>
<style lang="less" scoped>
  .service-list {
    min-height: 100vh;
    background-color: #F0F2F9;
    .mint-navbar {
      .mint-tab-item {
        font-size: 16px;
        padding: 20px 0 14.5px;
        border-bottom: 2px solid #E5E5E5;
      }
      .mint-tab-item.is-selected {
        color: #ffb717;
        border-color: #ffb717;
        margin-bottom: 0px;
      }
    }
    .mint-tab-item-label {
      font-size: 16px;
      color: #ffb717;
    }
    .mint-tab-container {
      .service-info {
        margin-bottom: 10px;
        padding: 7px 0;
        background-color: #fff;
        font-size: 14px;
        line-height: 23px;
        .com-list, .order {
          p {
            .text-right {
              float: right;
              font-size: 13px;
            }
            .text-red {
              color: #FF4848;
            }
            .text-green {
              color: #1EBB00;
            }
            .text-gray {
              color: #9D9DA5;
            }
            .text-orange {
              color: #FFB717;
            }
            .text-black {
              font-weight: 600;
            }
          }
        }
        .com-list {
          padding: 0 15px;
          border-bottom: 1px solid #E9ECF5;
        }
        .order {
          padding: 10px 15px;
          .text-right {
            border: 1px solid #FFB717;
            border-radius: 4px;
            line-height: 25px;
            padding: 0 16px;
          }
        }
      }
    }
  }
</style>



